import React, { useContext, useEffect, useState, useCallback } from "react";
import { useDispatch } from "react-redux";
import { ContentWrap, HeaderWrap, PageWrap } from "../../../styled/shared/wrap";
import { TextMiddle } from "../../../styled/shared";
import { useHistory } from "react-router-dom";

import { PageTransContext } from "../../../containers/pageTransContext";
import { BottomNavCloseAction } from "../../../reducers/container/bottomNav";

import icon_back from "../../../assets/icon-back-arrow.svg";
import { MainText } from "../../../styled/shared/text";
import { LoginInput } from "../../../styled/shared";
import styled from "styled-components";
import BottomButton from "../../../components/party/BottomButton";

const SignUpName = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  //context
  const { setPageTrans } = useContext(PageTransContext);

  //state
  const [name, setName] = useState("");

  // 
  const [namePageStatus, setNamePageStatus] = useState(false);
  //  
  const [pageConfirmStatus, setPageConfirmStatus] = useState(false);

  // 
  const [nameErrorText, setNameErrorText] = useState("");

  const closePage = () => {
    setPageTrans("trans toLeft");
    history.goBack();
  };

  useEffect(async () => {
    dispatch(BottomNavCloseAction);
  }, []);

  const handleName = useCallback(
    (e) => {
      setName(e.target.value);

      // 
      //  2~4 
      const reg = /^[-]{2,5}$/;

      if (!reg.test(e.target.value)) {
        setPageConfirmStatus(false);
        setNamePageStatus(false);
        setNameErrorText("  .");
        return;
      }

      setNameErrorText("");
      setPageConfirmStatus(true);
      setNamePageStatus(true);
    },
    [name]
  );

  return (
    <div className="page">
      <PageWrap>
        <HeaderWrap id="back_link" className="spoqaBold" onClick={closePage}>
          <div
            style={{
              position: "absolute",
              top: "55%",
              left: "1.25rem",
              transform: "translate(0,-55%)",
            }}
          >
            <img src={icon_back}></img>
          </div>
          <TextMiddle></TextMiddle>
        </HeaderWrap>
        <ContentWrap>
          <MainText>
             
            <br />
            <span className="yellowText"></span> .
          </MainText>
          <LoginInput
            value={name}
            onChange={handleName}
            className="spoqaBold"
            type="text"
            placeholder=" ."
          />
          <ErrorText className="spoqaRegular">{nameErrorText}</ErrorText>
          <BottomButton text={""} activeStatus={namePageStatus} isBottomStatus={true}/>
        </ContentWrap>
      </PageWrap>
    </div>
  );
};

export default SignUpName;

const ErrorText = styled.div`
    height: 1.0625rem;
    margin: 0.125rem 0 1.125rem 0;
    font-size: 0.6875rem;
    color: #fb5e5e;
    line-height: 1.0625rem;
`;
